.chatbox{
  border: 2px solid #b4b4b4;;
  border-radius: 10px;
  overflow-y: scroll;
  height: 90%;
  display: flex;
  flex-direction: column;
}

.inputarea{
  width: 90%;
  height: 10%;
}

.chatextcontainer{
  height: 90%;
  width: 70%;
  margin-left: 3vw;
  margin-right: 3vw;
  float: right;
}

.extcontainer{
  height: 80vh;
  width: 98vw;
  margin-left: 2vw;
  margin-right: 2vw;
}

.textinput{
  width: 87%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #b4b4b4;;
  scroll-behavior: auto;
  resize: none;
  margin-top: 5px;
  float: left;
}


.chatbutton{
  width: 5%;
  padding: 8px;
  margin-top: 5px;
  margin-right: 5px;
  margin-left: 5px;
  text-align: center;
  border: 2px solid #b4b4b4;
  border-radius: 10px ;
  font-size: 90%;
}

.statsbox{
  border: 2px solid #b4b4b4;
  border-radius: 3px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 10vh;
  height: 80%;
  width: 22%;
  float: left;
  align-items: center;
}
